<template>
   <div class="footer">
            <ul>
                <li>
                    <router-link :to="`/home?geohash=${this.geohash}`">
                        <p><i class="el-icon-eleme"></i></p>
                        <p>外卖</p>
                    </router-link>
                </li>
                <li>
                    <router-link :to="`/searched/${this.geohash}`">
                        <p><i class="el-icon-pie-chart"></i></p>
                        <p>搜索</p>
                    </router-link>
                   
                </li>
                <li>
                    <router-link to="/order">
                        <p><i class="el-icon-notebook-2"></i></p>
                        <p>订单</p>
                    </router-link>
                </li>
                <li>
                    <router-link to="/myuser">
                        <p><i class="el-icon-user"></i></p>
                        <p>我的</p>
                    </router-link>
                    
                </li>
            </ul>
        </div>
</template>

<script>
    export default {
        props:['not'],
        data() {
            return {
                geohash:""
            }
        },
        mounted () {
            this.geohash = JSON.parse(localStorage.getItem('geohash'))
            // console.log(this.geohash)
        }
    }
</script>

<style lang="scss" scoped>
.footer{
        width: 100%;
        height: 1rem;
        font-size: 0.28rem;
        ul{
            display: flex;
            li{
                i{
                    font-size: 0.4rem;
                }
                flex: 1;
                text-align: center;
                padding-top: 0.12rem;
                color: rgb(187, 166, 166);
                a{
                    color: rgb(187, 166, 166);
                }
            }
        }
    }
</style>


