<template>
    <div class="header">
        <!-- <slot name="search"></slot>
        <slot name="pass"></slot>
        <slot name="order"></slot> -->
        <!-- <slot name="receving"></slot> -->
        <div v-if="got==true" @click="$router.go(-1)"><i class="el-icon-arrow-left"></i></div>
        <div v-if="aot==true"><i class="el-icon-search"></i></div>
        <div v-if="ant==true">ele.me</div>
        <div v-if="amt==true"><h4>搜索</h4></div>
        <div v-if="cnt==true"><h4>我的</h4></div>
        <div v-if="cmt==true"><h4>订单列表</h4></div>
        <div v-if="clt==true"><h4>密码登录</h4></div>
        <div v-if="cut==true"><h4>账户信息</h4></div>
        <div v-if="nnt==true"><h4>编辑地址</h4></div>
        <div v-if="mnt==true"><h4>新增地址</h4></div>
        <div v-if="unt==true"><h4>搜索地址</h4></div>
        <div v-if="uot==true"><h4>确认订单</h4></div>
        <div class="gat"><h4><router-link to="/">{{gnt.name}}</router-link></h4></div>
        <div v-if="ont==true">编辑</div>
        <div v-if="cat==true" :style="{'display':bool?'none':'block'}" ><router-link to="/login">登录|注册</router-link></div>
        <div class="user" v-if="cat==true" :style="{'display':bool?'block':'none'}"><router-link to="/myuser"><van-icon name="user-o" /></router-link></div>
        <div v-if="cot==true" ><router-link to="/">切换城市</router-link></div>
        
        
    </div>
</template>
<script>
    export default {
        props:['got','gnt','gmt','aot','ant','amt','cot','cat','cnt','cmt','clt','cut','nnt','ont','mnt','unt','uot'],
        data () {
            return {
                id:"",
                bool:false
            } 
        },
        methods: {
           
        },
        mounted() {
            let id = this.$store.state.userInfo
            if(id!=''){
                this.bool=true
            }else{
                this.bool=false
            }
        },
    }
</script>

<style lang="scss" scoped>
.gat{
    width: 10rem;
}
.header{
        width: 100%;
        height: 1rem;
        background: rgb(192, 144, 236);
        line-height: 1rem;
        color: white;
        display: flex;
        font-size: 0.35rem;
        border: 1px solid white;
        div:nth-of-type(1){
            flex: 1;
            padding-left: 0.3rem;
        }
        div:nth-of-type(2){
            flex: 2;
            width: 2rem;
            text-align: center;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            // margin-left: 1.3rem;
            a{
                color: white;
            }
        }
        div:nth-of-type(3){
            flex:1;
            margin-left: 1.5rem;
            a{
                color: white;
            }
        }
    }
    .user{
        line-height: 1rem;
        // padding-left: 3rem;
        padding: 0 0.2rem;
        a{
            color: white;
        }
        
    }
</style>            


